import style from '../../CSS/adminNavbar/AddProductForm/categoryForm.module.css'
export default function CategoryForm({handleNext,handleBack}){
    return(
        <div className={style.categoryForm}>
            <div className={style.title}>
                <h3>Category</h3>
            </div>
            <div className={style.inputGroup}>
                <label className={style.label} htmlFor='categoryName'>Category Name</label>
                <input className={style.textInput} type='text' placeholder='Enter categoryName' />
            </div>

            <div className={style.ButtonGroup}>
                <button type='button' className={style.backButton} onClick={handleBack}>Back</button>
                <button type='button' className={style.nextButton} onClick={handleNext}>Next</button>
            </div>
        </div>
    )
}